<template>
  <div class="signup__inputData">
    <div v-if="isError" class="signup__inputData-error"></div>
    <form onsubmit="return false;">
      <label for="email">E-Mail</label><br>
      <input name="email" type="email" @keyup.enter="post" v-model="postData.email" required placeholder="Please Input Email Address"><br>
      <label for="password">Password</label><br>
      <input name="password" type="password" @keyup.enter="post" v-model="postData.password" required placeholder="Please Input Password"><br>
      <label for="location">Your Location</label><br><br>
      TOP
      <select class="signup__selectLocation" name="location" v-model="postData.location" options="options">
        <option v-for="option in options" :value="option.value">{{ option.text }}</option>
      </select>
      <div class="signup__selectSex">
        <br>
        <label for="sex">Gender</label><br><br>
        MEN <input type="radio" value="MEN" name="sex" required v-model="postData.sex">
        WOMEN <input type="radio" value="WOMEN" name="sex" required v-model="postData.sex">
      </div>
      <button class="signup__submit" @click="post">SignUp</button>
    </form>
  </div>
</template>

<script>
import axios from 'axios'

export default {
  methods: {
    post: function () {
      if (this.validation(this.postData)) {
        var self = this
        var baseUrl = '/api/v1/user/signup'
        var mail = '?mailAddress=' + encodeURIComponent(this.postData.email)
        var password = '&password=' + this.postData.password
        var location = '&loc=' + this.postData.location + ',jp'
        var gender = '&gender=' + this.postData.sex

        var url = baseUrl + mail + password + location + gender

        axios.post(url)
        .then(function (response) {
          if (response.status === 200) {
            window.location = '/#/signin'
          }
        })
        .catch(function (error) {
          console.log(error.response.status)
          if (error.response.status === 400) {
            // 
            self.postData.email = ''
            self.postData.password = ''
            self.postData.location = 'tokyo'
            self.postData.sex = ''
            // TODO: 
            self.isError = true
          }
        })
      }
    },
    validation: function (data) {
      if (data.email !== '' && data.password !== '' && data.sex !== '') {
        return true
      } else {
        return false
      }
    }
  },
  data () {
    return {
      isError: false,
      postData: {
        email: '',
        password: '',
        sex: '',
        location: 'tokyo'
      },
      options: [
        {value: 'Sapporo', text: ''},
        {value: 'aomori', text: ''},
        {value: 'Morioka', text: ''},
        {value: 'Sendai', text: ''},
        {value: 'akita', text: ''},
        {value: 'yamagata', text: ''},
        {value: 'fukushima', text: ''},
        {value: 'Mito', text: ''},
        {value: 'Utsunomiya', text: ''},
        {value: 'Maebashi', text: ''},
        {value: 'saitama', text: ''},
        {value: 'chiba', text: ''},
        {value: 'tokyo', text: ''},
        {value: 'Yokohama', text: ''},
        {value: 'niigata', text: ''},
        {value: 'toyama', text: ''},
        {value: 'Kanazawa', text: ''},
        {value: 'fukui', text: ''},
        {value: 'yamanashi', text: ''},
        {value: 'nagano', text: ''},
        {value: 'gifu', text: ''},
        {value: 'shizuoka', text: ''},
        {value: 'Nagoya', text: ''},
        {value: 'Tsu', text: ''},
        {value: 'Otsu', text: ''},
        {value: 'kyoto', text: ''},
        {value: 'osaka', text: ''},
        {value: 'Kobe', text: ''},
        {value: 'nara', text: ''},
        {value: 'wakayama', text: ''},
        {value: 'tottori', text: ''},
        {value: 'Matsue', text: ''},
        {value: 'okayama', text: ''},
        {value: 'hiroshima', text: ''},
        {value: 'yamaguchi', text: ''},
        {value: 'tokushima', text: ''},
        {value: 'Takamatsu', text: ''},
        {value: 'Matsuyama', text: ''},
        {value: 'kochi', text: ''},
        {value: 'fukuoka', text: ''},
        {value: 'saga', text: ''},
        {value: 'nagasaki', text: ''},
        {value: 'kumamoto', text: ''},
        {value: 'oita', text: ''},
        {value: 'miyazaki', text: ''},
        {value: 'kagoshima', text: ''},
        {value: 'Naha', text: ''}
      ]
    }
  }
}
</script>

<style>
.signup__inputData {
  width: 80%;
  height: 90%;
  margin: 5% auto;
  font-size: 3em;
}

.signup__inputData input {
  font-size: 1em;
  border: 0;
  width: 100%;
  margin: 5% 0;
  border-bottom: 3px solid #01288d;
}

.signup__selectLocation {
  -webkit-appearance: none;
	-moz-appearance: none;
	appearance: none;

  margin-top: 5%;
  padding: 3%;
  width: 100%;
  height: 8%;
  border: 0;
  background-color: #01288d;
  color: white;
  font-size: 1.5em;
}

.signup__selectLocation option {
    font-size: 30%;
}

.signup__submit {
  font-size: 1.3em;
  border: 0;
  margin: 5% 0;
  padding: 1% 3%;
  position: absolute;
  right: 10%;
  background-color: darkorange;
  color: #ffffff;
}

.signup__selectSex {
  border-bottom: 4px solid #01288d;
}

.signup__selectSex input {
  width: 1em;
  height: 1em;
  margin: 2% 5%;
}

.signup__inputData-error {
  background-color: indianred;
  width: 90%;
  text-align: center;
  padding: 5% 2%;
  margin: 2% auto; 
  border-radius: 10px;
  color: #ffffff;
}
</style>



